.advertisement {
  color: #ffffff;
  background-color: #68b738;
  background: url("../../img/advertisement/background-mobile.jpg") no-repeat;
}

.advertisement__title {
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  text-align: center;
  margin: 0;
  .font-style-title();
}

.advertisement__button {
  font-size: 14px;
  line-height: 18px;
  display: block;
  text-align: center;
  margin: 0 auto;
  box-sizing: border-box;
  min-width: 280px;
  min-height: 40px;
  padding: 7px 44px 7px 44px;
}

